@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --bg-primary: #f8f8f8;
  --bg-secondary: #f5f5f4;
  --bg-tertiary: #e7e5e4;
  --text-primary: #292524;
  --text-secondary: #57534e;
  --text-tertiary: #78716c;
  --accent-primary: #f59e0b;
  --accent-secondary: #fbbf24;
  --border-primary: #e7e5e4;
  --border-secondary: #d6d3d1;
  --shadow-color: rgba(0, 0, 0, 0.1);
  --grain-opacity: 0.1;
}

.dark {
  --bg-primary: #1E1E2E;
  --bg-secondary: #2A2A3C;
  --bg-tertiary: #313244;
  --text-primary: #CDD6F4;
  --text-secondary: #A6ADC8;
  --text-tertiary: #7F849C;
  --accent-primary: #F5A97F;
  --accent-secondary: #FAB387;
  --border-primary: #45475A;
  --border-secondary: #585B70;
  --shadow-color: rgba(0, 0, 0, 0.3);
  --grain-opacity: 0.05;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: background-color 0.3s ease, color 0.3s ease;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.bg-grain {
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.9' numOctaves='4' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");
  opacity: var(--grain-opacity);
}

/* Dark mode note colors */
.dark .bg-amber-100 {
  @apply bg-dark-100 border-dark-300;
}
.dark .bg-rose-100 {
  @apply bg-dark-100 border-dark-300;
}
.dark .bg-orange-100 {
  @apply bg-dark-100 border-dark-300;
}
.dark .bg-stone-100 {
  @apply bg-dark-100 border-dark-300;
}
.dark .bg-yellow-100 {
  @apply bg-dark-100 border-dark-300;
}

/* Dark mode text colors */
.dark .text-stone-800 {
  @apply text-dark-900;
}
.dark .text-stone-700 {
  @apply text-dark-800;
}
.dark .text-stone-600 {
  @apply text-dark-700;
}
.dark .text-stone-500 {
  @apply text-dark-600;
}
.dark .text-stone-400 {
  @apply text-dark-500;
}

/* Dark mode transitions */
.theme-transition {
  transition-property: background-color, border-color, color, fill, stroke;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 300ms;
}
